.logo {
  height: 100px;
}

.site-header {
  padding-top: 0 !important;
}

.btn, .btn:link, .btn:visited {
  text-decoration: none;
  font-size: 1.8rem;
  padding: 1.5rem 3rem;
  border-radius: 1.5rem;
  border: none;
  text-transform: uppercase;
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
  will-change: transform;
  box-shadow: 0.2rem 0.6rem 1rem rgba(51, 51, 51, 0.15);
}
.btn:hover, .btn:link:hover, .btn:visited:hover {
  transform: translateY(-2px);
  box-shadow: 0.2rem 1.2rem 1rem rgba(51, 51, 51, 0.15);
}
.btn--black, .btn:link--black, .btn:visited--black {
  background-color: #333;
  color: #fff;
}